<template>
    <div class="c-editor-markdown-dialog ">
        <span class="c-markdown-toolbar-file c-markdown-toolbar-item" @click="handleClick" title=""></span>

        <el-dialog :visible.sync="visible" title="" :modal-append-to-body="false">

            <p> <a target="_blank" href="/tool/32032"></a></p>

            <el-input class="u-input" type="textarea" :rows="4" v-model="pzCode"></el-input>

            <div slot="footer">
                <el-button @click="cancel"></el-button>
                <el-button type="primary" @click="insert"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'markdown_marco',
    data() {
        return {
            visible: false,
            pzCode: ''
        }
    },
    methods: {
        handleClick() {
            this.visible = true
        },
        cancel() {
            this.visible = false
            this.pzCode = ''
        },
        insert() {
            const content = `${this.pzCode}`

            this.$emit('insert', content)

            this.pz = ''

            this.visible = false
        }
    }
}
</script>
